@import "./../../constants.scss";

.auth__modal {
  width: 100%;
  overflow: hidden;
  backdrop-filter: blur(1px);

  .modal__wrapper {
    position: relative;
    max-width: $content-width;
    margin: 0 auto;
    padding: 6rem;
    background: rgba(39, 39, 39, 0.753);
  }

  .modal {
    position: relative;
    max-width: 69rem;
    min-height: 35rem;
    background-color: $color-dark;
    padding: 6.5rem 8rem;
    margin: 8rem auto;
    box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.1), 0px 2px 10px rgba(0, 0, 0, 0.05), 0px 10px 20px rgba(0, 0, 0, 0.05),
      0px 10px 50px rgba(0, 0, 0, 0.05);
    border-radius: 4px;

    &__title {
      margin-bottom: 4rem;
      position: relative;
    }

    &__subtitle {
      margin-bottom: 5rem;
      font-size: 2rem;
      font-weight: 300;
    }

    &-button {
      display: flex;
      justify-content: flex-end;

      .reset__button {
        margin-right: 1.3rem;
      }
    }

    &-footer {
      position: absolute;
      left: 50%;
      transform: translate(-50%);
      bottom: 0;
      width: 100%;
      max-width: $content-width;
      margin: 0 auto;
    }

    &__form {
      font-size: 2rem;
      font-weight: 275;
      font-family: $font-family;

      h3 {
        font-size: 2rem;
        font-weight: 275;
        font-family: $font-family;
      }

      &-link {
        color: $color-light;
        padding-bottom: 2px;
      }
    }
    &-close {
      background: url("./../../assets/cross.svg");
      width: 2.2rem;
      height: 2.2rem;
      background-position: center right;
      background-size: cover;
      background-repeat: no-repeat;
      position: absolute;
      top: 2.6rem;
      right: 2.6rem;
      cursor: pointer;
    }
  }
}

.auth__form {
  position: relative;
  width: 100%;

  label {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-self: center;
    font-size: 1.6rem;
    font-weight: 600;
    text-transform: uppercase;
    color: $color-primary;
    margin-bottom: 3.7rem;
    letter-spacing: 1px;
  }

  .form-input {
    width: 100%;
    font-size: 2rem;
    font-weight: 275;
    color: $color-light;
    font-family: $font-family;
    letter-spacing: 1px;
    margin-top: 1.3rem;
    padding: 1.7rem;
    background-color: $color-light-grey;
    border: none;
    outline: none;
    opacity: 0.8;
    border-radius: 4px;
    text-align: left;
  }
}

.form_email {
  position: relative;
  .form__error {
    position: absolute;
    top: 9.3rem;
    color: red;
    font-size: 1.4rem;
    text-transform: capitalize;
  }
}

.form_password {
  position: relative;
  .form__error {
    position: absolute;
    top: 9.3rem;
    color: red;
    font-size: 1.4rem;
    text-transform: capitalize;
  }
}
